<!-- 文件路径：frontend/src/components/AppDashboard.vue -->
<template>
  <div>
    <el-container>
      <!-- 左侧菜单 -->
      <el-aside width="200px" style="background-color: #2D3A4B; padding: 0;">
        <el-menu default-active="1" class="el-menu-vertical-demo" background-color="#2D3A4B" text-color="#fff" active-text-color="#ffd04b">
          <el-menu-item index="1">Dashboard</el-menu-item>
          <el-menu-item index="2">Recipes</el-menu-item>
          <el-menu-item index="3">Ingredients</el-menu-item>
        </el-menu>
      </el-aside>

      <!-- 右侧内容 -->
      <el-container>
        <el-header style="background-color: #409EFF; color: #fff; padding: 0; display: flex; justify-content: center; align-items: center; height: 100px;">
          <h2>严总配料计算器</h2>
        </el-header>

        <el-main>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-card shadow="hover">
                <div>食谱统计: {{ recipesCount }}</div>
              </el-card>
            </el-col>
            <el-col :span="12">
              <el-card shadow="hover">
                <div>配料统计: {{ ingredientsCount }}</div>
              </el-card>
            </el-col>
          </el-row>
        </el-main>

        <!-- 页脚 -->
        <el-footer style="text-align: center; padding: 10px;">
          &copy; 2024 Your Company. All rights reserved.
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import { ref, onMounted } from "vue";

export default {
  name: "AppDashboard",
  setup() {
    const recipesCount = ref(0);
    const ingredientsCount = ref(0);

    const getRecipesCount = async () => {
      try {
        const response = await axios.get("http://localhost:8080/api/recipes/count");
        recipesCount.value = response.data;
      } catch (error) {
        console.error("Failed to fetch recipes count:", error);
      }
    };

    const getIngredientsCount = async () => {
      try {
        const response = await axios.get("http://localhost:8080/api/ingredients/count");
        ingredientsCount.value = response.data;
      } catch (error) {
        console.error("Failed to fetch ingredients count:", error);
      }
    };

    onMounted(() => {
      getRecipesCount();
      getIngredientsCount();
    });

    return {
      recipesCount,
      ingredientsCount,
    };
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.el-container {
  height: 100vh;
}

.el-aside {
  height: 100%;
}

.el-header {
  text-align: left;
}
</style>
